.demo-wrapper {
  width: 100%;
  height: 1150px;
  background-color: #21ce99;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.demo-container {
  width: 1040px;
  height: 845px;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.demo-header {
  color: white;
  font-size: 38px;
  font-weight: 100;
  line-height: 1.5;
  letter-spacing: 1.5px;
  text-align: center;
  margin-bottom: 90px;
}
.second-video-wrapper {
  height: 700px;
  width: 500px;
  background-image: url('../assets/whitephone.png');
  background-repeat: no-repeat;
  position: relative;
  margin-left: 180px;
}
.second-video-container {
  height: 650px;

}
.second-video {
  left: 4%;
  top: 12.3%;
}
.demo-content-container {
  height: 600px;
  width: 280px;
  margin-left: 350px;
}
.demo-content {
  height: 380px;
  width: 280px;
  color: white;
  font-size: 21px;
  font-weight: 100;
  line-height: 1.5;
  letter-spacing: 1.2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-direction: column;
      flex-direction: column;
}
.demo-learn-button-container {
  width: 280px;
  height: 150px;
  margin: 20px 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: top;
      align-items: top;
  -ms-flex-direction: column;
      flex-direction: column;
}
.learn-button {
  width: 100%;
  height: 50px;
  border: solid 1.2px white;
  border-radius: 4px;
  text-align: center;
  color: white;
  font-weight: 300;
  font-size: 15px;
  line-height: 50px;
  cursor: pointer;
}
.learn-button:hover {
  background: white;
  color: #21ce99;
}



