.nav,
.nav-scrolled {
  width: 100%;
  height: 92px;
}
.nav-container {
  width: 1040px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.nav-logo {
  width: 35px;
  height: 90px;
  background-image: url('../assets/logo.png');
  background-position: center;
  background-repeat: no-repeat;
}

.nav-buttons-container {
  height: 92px;
  width: 463.23px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  color: #21ce99;
  font-size: 13.5px;
  padding-left: 80px;
  text-transform: uppercase;
  font-weight: 320;
}
.signup-button {
  background: #21ce99;
  width: 82.98px;
  height: 39px;
  color: white;
  font-weight: 400;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 5px;
}
.signup-button:hover {
  cursor: pointer;
}
.route-button:hover {
  cursor: pointer;
}

.nav-scrolled {
  -webkit-transition: top 1s;
  -o-transition: top 1s;
  transition: top 1s;
  position: fixed;
  background: rgba(255, 255, 255, 0.8);
  z-index: 999;
}



